html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  font-family: 'Barlow Condensed', sans-serif;
  background-size: cover;
}

a {
  text-decoration: none;
}

.layout {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0px;
  background-color: #ededed;
  line-height: 18px;
  margin-top: 30px;
}

.container {
  display: flex;
  flex-direction: row;
  margin: 30px;
}

.blog {
  margin-right: 32px;
}

.post-list {
  margin-top: 45px;
}

.facebook, .twitter {
  background: url("../img/sprite.png") no-repeat;
  overflow: hidden;
  margin-right: 5px;
  display: inline-block;
}

.facebook {
  background-position: -0px -0px;
  width: 16px;
  height: 16px;
}

.twitter {
  background-position: -0px -16px;
  width: 16px;
  height: 16px;
}

.facebook-big, .rss-big, .twitter-big {
  display: inline-block;
  background: url("../img/sprite-big.png") no-repeat;
  overflow: hidden;
}

.facebook-big {
  background-position: -0px -0px;
  width: 32px;
  height: 32px;
}

.rss-big {
  background-position: -0px -32px;
  width: 32px;
  height: 32px;
}

.twitter-big {
  background-position: -0px -64px;
  width: 32px;
  height: 32px;
}

.header {
  padding: 25px 0 25px 32px;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: row;
  background-position: center center;
  background-size: cover;
}

.header__logo {
  margin-right: 45px;
}

.header__site-logo {
  width: 140px;
  height: 58px;
  background-repeat: no-repeat;
  border-bottom: 1px solid #6892a0;
  display: inline-block;
}

.header__site-title {
  font-family: 'Lucida Sans Unicode', arial;
  text-transform: uppercase;
  font-size: 10px;
  color: #446c7a;
  padding: 10px 0 0 0;
  text-align: center;
  width: 140px;
}

.header__topnav a {
  text-decoration: none;
  color: #a6a6a6;
  margin-right: 10px;
  text-transform: uppercase;
  font-size: 10px;
}

.header__botnav {
  padding-top: 22px;
  margin-left: -16px;
}

.header__botnav a {
  text-decoration: none;
  color: #fff;
  text-transform: uppercase;
  font-size: 14px;
  padding: 10px 16px;
  display: inline-block;
}

.header__botnav a:hover {
  background-color: transparent;
  box-shadow: inset 1px 0 1px #07252f;
}

.topbar {
  display: flex;
  background: #fff;
  background: linear-gradient(#ffffff 0%, #d9dbdc 100%);
  padding: 10px 0px 10px 0px;
  width: 100%;
  font-size: 14px;
  flex-wrap: wrap;
}

.topbar a {
  text-decoration: none;
  color: #757575;
}

.topbar__wraper {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.topbar__tag {
  display: inline-block;
  text-transform: uppercase;
  background-color: #85afbd;
  border-radius: 2px;
  color: #fff;
  margin: 0 0 0 32px;
  padding: 1px 3px;
}

.topbar__link {
  margin-left: 12px;
  font-style: italic;
  font-family: 'Georgia', sans-serif;
  color: #757575;
  flex-grow: 1;
}

.topbar__arrow {
  margin-left: 30px;
}

.topbar__search {
  display: flex;
  margin-left: 88px;
}

.topbar__search input {
  height: 26px;
  padding: 0 30px;
  border-radius: 26px;
  font-style: italic;
  outline: none;
  color: #757575;
}

.topbar__search-img {
  width: 18px;
  height: 18px;
  background-position: center center;
  background-size: cover;
  margin-top: 6px;
  margin-left: -30px;
}

.main-title {
  padding: 8px;
  background-color: #ae0002;
  font-size: 18px;
  color: #fff;
  text-transform: uppercase;
  border-radius: 3px;
  overflow: hidden;
}

.post-title {
  display: flex;
  flex-direction: row;
  align-items: center;
  background-color: #cacaca;
  border-radius: 3px;
  overflow: hidden;
}

.post-title__l {
  padding-right: 35px;
  border-radius: 3px 0px 0px 3px;
}

.post-title__arrow {
  margin-left: -10px;
  padding: 17px;
  background-color: #ae0002;
  border-radius: 1px;
  transform: rotate(45deg);
}

.post-title__r {
  color: #757575;
  font-family: 'Georgia', sans-serif;
  font-size: 12px;
  padding-left: 18px;
  white-space: nowrap;
  overflow: hidden;
}

.title a {
  text-decoration: underline;
  color: #042b38;
  font-size: 12px;
  font-family: 'Georgia', sans-serif;
}

.main-post {
  background-position: center center;
  background-size: cover;
  border-radius: 3px;
  margin-top: 5px;
  padding: 108px 0px 37px 40px;
}

.main-post__description {
  max-width: 340px;
  padding: 25px 15px;
  background-color: #1e1e1e;
  color: #fff;
  font-size: 12px;
  font-family: 'Georgia', sans-serif;
  line-height: 18px;
  text-align: justify;
}

.post-slider {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-top: 15px;
}

.post-slider__news {
  background-color: #fff;
  padding: 15px 16px 10px 15px;
  margin-right: 15px;
  border-radius: 3px;
  box-shadow: inset 1px 0px 1px #d4d4d4;
}

.post-slider__news:last-child {
  margin-right: 0px;
}

.post-slider__preview {
  height: 118px;
  width: 100%;
  background-position: center center;
  background-size: cover;
}

.post-slider__title {
  padding-top: 15px;
  display: inline-block;
}

.block-content {
  padding-top: 15px;
  display: inline-block;
  font-size: 11px;
  font-family: 'Georgia', sans-serif;
  font-style: italic;
  color: #757575;
}

.comments-counter {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
}

.comments-counter span {
  font-size: 11px;
  font-family: 'Georgia', sans-serif;
  font-style: italic;
  color: #989898;
  padding-left: 5px;
}

.comments-counter.regular span {
  font-size: 12px;
  font-style: normal;
}

.post {
  display: flex;
  margin-top: 30px;
}

.post__preview {
  height: 130px;
  width: 100%;
  background-position: center center;
  background-size: cover;
}

.post__content {
  padding: 12px 0 0 25px;
  font-size: 12px;
  font-family: 'Georgia', sans-serif;
  color: #757575;
}

.post__informers {
  padding: 25px 0 31px 0;
  display: flex;
}

.post__share {
  display: flex;
  padding-left: 30px;
  flex-wrap: wrap;
}

.post__share span {
  display: inline-block;
  padding-right: 15px;
}

.post__tag {
  padding-left: 25px;
}

.post__tag span {
  padding-right: 10px;
}

.post__tag a {
  text-decoration: underline;
  font-style: italic;
}

.hottest-video__preview {
  margin-top: 5px;
  border-radius: 3px;
  height: 120px;
  width: 100%;
  background-position: center center;
  background-size: cover;
}

.hottest-video__inner {
  display: flex;
  flex-direction: column;
  margin-bottom: 5px;
}

.video-btn {
  width: 36px;
  height: 36px;
  position: absolute;
  margin-top: 74px;
  margin-left: 10px;
}

.subscribe__mail {
  display: flex;
  justify-content: space-between;
  background: #fff;
  background: linear-gradient(#ffffff 0%, #d9dbdc 100%);
  width: 100%;
  font-size: 14px;
  flex-wrap: wrap;
  border-radius: 3px;
  box-shadow: inset 1px 0px 1px #d4d4d4;
  margin: 10px 0;
}

.subscribe__link {
  margin: 10px;
}

.subscribe__link a {
  margin: 10px;
  font-style: italic;
  color: #757575;
}

.subscribe__mail-img {
  height: 18px;
  width: 18px;
  margin: 10px 10px;
  background-position: center center;
  background-size: cover;
}

.footer {
  display: flex;
  max-width: 1000px;
  margin: 0 auto;
  padding: 100px 0;
  line-height: 18px;
  margin-top: 30px;
  justify-content: space-around;
}

.footer__nav {
  display: flex;
}

.footer__nav h3 {
  margin-bottom: 25px;
}

.footer__nav h3 a {
  color: #406474;
  font-size: 12px;
  text-transform: uppercase;
}

.footer__nav li a {
  color: #7b7d7e;
  font-size: 12px;
  font-family: 'Georgia', sans-serif;
  text-decoration: underline;
}

.footer__site-logo {
  width: 140px;
  height: 58px;
  background-repeat: no-repeat;
  border-bottom: 1px solid #6892a0;
  display: inline-block;
}

.col {
  padding: 0 5px;
}

@media only screen and (max-width: 992px) {
  .container {
    flex-wrap: wrap;
  }
  .hottest-video__inner {
    flex-direction: row;
    justify-content: space-around;
  }
  .post-title {
    flex-wrap: wrap;
  }
  .post-title__l {
    flex-wrap: wrap;
  }
  .hottest-video__post {
    margin-right: 15px;
  }
  .hottest-video__post:last-child {
    margin-right: 0px;
  }
  .blog {
    margin-right: 0;
  }
  .post-slider {
    flex-wrap: wrap;
  }
  .post-slider__news {
    width: 43%;
    margin-right: 0;
    margin-bottom: 5px;
  }
}

@media only screen and (max-width: 770px) {
  .post-title {
    background-color: #ae0002;
    font-size: 18px;
    color: #fff;
    text-transform: uppercase;
    border-radius: 3px;
    overflow: hidden;
  }
  .post-title__arrow {
    display: none;
  }
  .post-title__r {
    display: none;
  }
  .footer__logo {
    display: none;
  }
}

@media only screen and (max-width: 520px) {
  .post-slider__news {
    padding: 10px;
    width: 100%;
    margin-right: 0;
    margin-bottom: 5px;
  }
  .post {
    flex-direction: column;
  }
  .post__content {
    padding: 12px;
  }
  .header {
    flex-wrap: wrap;
  }
  .main-post {
    padding: 60px 20px;
  }
  .footer {
    display: none;
  }
}
